import React from "react"
import cx from "classnames"

import config from "../config"

class InternalLink extends React.Component {

    onClick = ev => {

        const { uri } = this.props;

        const { appName, contextPath } = config;

        if (contextPath && uri.indexOf(contextPath) !== 0)
        {
            // if we have a context path, we only intercept when the link starts with the context path.
            return;
        }

        const baseSegment = contextPath + "/" + appName;
        if (uri.indexOf(baseSegment) !== 0)
        {
            // we don't intercept if the link goes to another end-point
            return;
        }

        ev.preventDefault();



    };

    render()
    {
        const { uri, className, children } = this.props;

        return (
            <a
                className={
                    cx("link-internal", className)
                }
                href={ uri }
                onClick={ this.onClick }
            >
                {
                    children
                }
            </a>
        )
    }
}


export default InternalLink
